.logo{
    width: 110px;
    height: 50px;
}

.my-nav{
    position: fixed;
    z-index: 10;
    width: 100%;
}

.carousel_img{
    width: 100%;
    height: 100vh;
}

.nav-link{
    font-weight: 600;  
    transition: 0.1s ease;
    margin: 0 8px;
}

.nav-link:hover{
    font-weight: 700;
    border-bottom: 3px solid black;
}

.nav-item .active{
    font-weight: 700;
    border-bottom: 3px solid black;
}

.previous,
.next{
    width: 50px;
    height: 50px;
    background-color: rgba(0, 0, 0, 0.322) !important;
    border-radius: 50%;
    transition: 0.3s ease;
}

.previous:hover,
.next:hover{
    background-color: rgba(0, 0, 0, 0.637) !important;
}

.LoginBtn,
.SignUpBtn{
    font-weight: 600;
    margin: 0 10px;
}


@media (max-width:990px){
    .navbar{
        background-color: rgba(170, 170, 250, 0.5);
    }
    .dropdown-menu{
        background-color: rgba(170, 170, 250, 0.5);
    }
}