.peak{
    width: 100%;
    background: url('../images/wave-haikei.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}


.aboutHeader{
    margin-bottom: 70px;
    font-weight: 900;
}


.aboutImg{
    width: 100%;
    height: 100%;
    border-radius: 20px;
    box-shadow: 3px 5px 7px rgba(0,0,0,0.5);
}

.testimonialsContainer{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.testimonials{
    position: relative;
    max-width: 1000px;
    width: 100%;
    padding: 50px 0;
}

.clientImage{
    height: 200px;
    width: 200px;
    object-fit: cover;
    border-radius: 50%;
}

.slide{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    row-gap: 20px;
}

.slide p{
    padding: 0 80px;
    text-align: center;
    font-size: 13pt;
    font-weight: 400;
    color: #333;
}

.details{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.name{
    font-size: 16pt;
    font-weight: 600;
    color: #333;
}

.jobtitle{
    font-size: 13pt;
    font-weight: 500;
    color: #333;
}


@media(max-width:770px){
    .aboutParagraph{
        margin-bottom: 50px;
    }
}