.steps{
    width: 100%;
    background: url('../images/layered-steps-haikei.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.contact{
    margin-bottom: 30px;
    background-color: rgba(247, 247, 247, 0.445);
    border-radius: 20px;
}


.contactHeader{
    margin: 50px 0;
    font-weight: 900;
}


.contactParagraph{
    font-size: 15pt;
    margin-bottom: 60px;
}

.contactNumber{
    margin-top: 40px;
}

.contactNumber,
.emailAddress,
.location,
.businessHours{
    margin-bottom: 15px;
    color: rgb(0, 0, 0);
    font-weight: 600;
    font-size: 13pt;
}

.contactNumber p,
.emailAddress p,
.location p,
.businessHours p{
    margin-bottom: 20px;
    color: rgb(19, 2, 2);
    font-weight: 600;
    font-size: 13pt;
}

.bookAppointment{
    margin-bottom: 30px;
    background-color: rgba(133, 133, 133, 0.445);
    border-radius: 20px;
}

.appointment{
    margin: 30px 0;
}

#fullname,
#email{
    border-radius: 5px;
    border: none;
}

.submitBtn{
    width: 150px;
    font-weight: 700;
}

@media(min-width:575px){
    .contactNumber i,
.emailAddress i,
.location i,
.businessHours i{
    text-align: end;
}
}
