body{
    background-color: #c8e0f3;
}
.servicesHeader{
    margin: 70px 0;
    font-weight: 900;
}

.whatWeDo{
    font-weight: 700;
    font-size: 20pt;
}

.whatWeParagraph{
    font-size: 15pt;
    margin-bottom: 60px;
}

.card_container{
    display: flex;
    justify-content: center;
}

.card{
    width: 18rem;
    margin: 0 18px;
    box-shadow: 5px 8px 11px rgba(0,0,0,0.5);
}

.card_carousel{
    height: 50%;
}


.wave{
    width: 100%;
    height: 100vh;
    background: url('../images/layered-waves-haikei.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}


@media (max-width:425px){

    .wave{
        margin-bottom: 50px;
    }

    .card{
        width: 91%;
        margin-bottom: 50px;
    }
}